/* Import */
import { useEffect, useContext } from "react";
import { DataChanger_Object } from "../../utils/helpers.js";
/* Component */
import CommonOptionWrapper from "./CommonOptionWrapper.js";
import CommonOptionContent from "./CommonOptionContent.js";
import CommonItemWrapper from "./CommonItemWrapper.js";
import CommonItemContent from "./CommonItemContent.js";
import ListOptionContent from "./ListOptionContent.js";
import CheckItem from "./CheckItem.js";
import TextareaWapperAttend from "./TextareaWapperAttend.js";
import TextareaWapperCount from "./TextareaWapperCount.js";
import RadioItem from "./RadioItem.js";
import SettingNotice from "../layout/SettingNotice.js";
import SettingNoticeContent from "../layout/SettingNoticeContent.js";
import InputBasic from "../layout/InputBasic.js";
/* Context */
import { SetContext } from "../../store/option-set-context.js";

// C:    
const AttendSetting = () => {
  const { attendList, setAttendList } = useContext(SetContext);
  useEffect(() => { //       
    if(attendList.optionAttendMeal === false) {
      setAttendList((prev) => {
        return {
          ...prev,
          "attendMealNotice": "",
          "attendNoMealNotice": "",
        }
      })
    }
    if(attendList.optionAttendBus === false) {
      setAttendList((prev) => {
        return {
          ...prev,
          "attendBusNoticeM": "",
          "attendNoBusNoticeM": "",
          "attendBusNoticeF": "",
          "attendNoBusNoticeF": ""
        }
      })
    }
  }, [setAttendList, attendList.optionAttendMeal, attendList.optionAttendBus]);
  return (
    <CommonOptionWrapper>
      <CommonOptionContent>
        <CommonItemWrapper>
          <CommonItemContent title="">
            <InputBasic 
              inputType="text"
              inputName="attendTitle" 
              inputValue={attendList.attendTitle} 
              maxLength={10}
              onChange={(e) => DataChanger_Object(e, setAttendList)}
              onClear={setAttendList}
              placeholder="  "
              inputStyle=""
            />
          </CommonItemContent>
        </CommonItemWrapper>

        <CommonItemWrapper>
          <CommonItemContent title="" multi={true}>
            <TextareaWapperCount
              name="attendContent" 
              id="attendContent" 
              value={attendList.attendContent} 
              onChange={(e) => DataChanger_Object(e, setAttendList)}
              onClear={setAttendList}
              placeholder=" ." 
              maxLength={100} 
            />
          </CommonItemContent>
        </CommonItemWrapper>

        <CommonItemWrapper>
          <CommonItemContent title="">
            <InputBasic 
              inputType="text"
              inputName="attendButtonTitle" 
              inputValue={attendList.attendButtonTitle} 
              maxLength={10}
              onChange={(e) => DataChanger_Object(e, setAttendList)}
              onClear={setAttendList}
              placeholder="  " 
            />
          </CommonItemContent>
        </CommonItemWrapper>

        <CommonItemWrapper>
          <CommonItemContent title="" multi="check">
            <CheckItem name="optionAttendName" type="attend" content="" id="optionAttendName" />
            <CheckItem name="optionAttendCount" type="attend" content="" id="optionAttendCount" />
            <CheckItem name="optionAttendMeal" type="attend" content=" " id="optionAttendMeal" />
            <ListOptionContent>
              {
                attendList.optionAttendMeal ?
                <CommonItemWrapper>
                  <TextareaWapperAttend 
                    content="  " 
                    id="attendMealNotice" 
                    value={attendList.attendMealNotice} 
                    onChange={(e) => DataChanger_Object(e, setAttendList)}
                    onClear={setAttendList}
                    placeholder="     .&#10;)    ." 
                  />
                  <TextareaWapperAttend 
                    content="  " 
                    id="attendNoMealNotice" 
                    value={attendList.attendNoMealNotice} 
                    onChange={(e) => DataChanger_Object(e, setAttendList)} 
                    onClear={setAttendList}
                    placeholder="     .&#10;)        ." 
                  />
                </CommonItemWrapper> : null
              }
              <CommonItemWrapper>
                <CheckItem name="optionAttendBus" type="attend" content="  " id="optionAttendBus"></CheckItem>
                {
                  attendList.optionAttendBus ?
                  <>
                    <TextareaWapperAttend 
                      content="  " 
                      id="attendBusNoticeM" 
                      value={attendList.attendBusNoticeM} 
                      onChange={(e) => DataChanger_Object(e, setAttendList)} 
                      onClear={setAttendList}
                      gender="M" 
                      division="()" 
                      placeholder="     .&#10;)   11  1   " 
                    />
                    <TextareaWapperAttend 
                      content="  " 
                      id="attendNoBusNoticeM" 
                      value={attendList.attendNoBusNoticeM} 
                      onChange={(e) => DataChanger_Object(e, setAttendList)} 
                      onClear={setAttendList}
                      gender="M" 
                      division="()" 
                      placeholder="     .&#10;)     ." 
                    />
                    <TextareaWapperAttend 
                      content="  " 
                      id="attendBusNoticeF" 
                      value={attendList.attendBusNoticeF} 
                      onChange={(e) => DataChanger_Object(e, setAttendList)} 
                      onClear={setAttendList}
                      gender="F" 
                      division="()" 
                      placeholder="     .&#10;)   11  1   " 
                    />
                    <TextareaWapperAttend 
                      content="  " 
                      id="attendNoBusNoticeF" 
                      value={attendList.attendNoBusNoticeF} 
                      onChange={(e) => DataChanger_Object(e, setAttendList)} 
                      onClear={setAttendList}
                      gender="F" 
                      division="()" 
                      placeholder="     .&#10;)     ." 
                    />
                  </> : null
                }
              </CommonItemWrapper>
              {/* <CommonItemWrapper>
                <CheckItem content=" " id="optionAttendMsg"></CheckItem>
                <TextareaWapperAttend content=" " id="attendMsgNoticeM" gender="M" division=""></TextareaWapperAttend>
                <TextareaWapperAttend content=" " id="attendMsgNoticeF" gender="F" division=""></TextareaWapperAttend>
              </CommonItemWrapper> */}
            </ListOptionContent>
          </CommonItemContent>
        </CommonItemWrapper>
      </CommonOptionContent>

      <CommonOptionContent>
        <CommonItemWrapper>
          <CommonItemContent title=" " multi="check">
            <RadioItem name="optionAttendPopup" id="optionAttendPopupOpen" content="     " radioidx="1" />
            <RadioItem name="optionAttendPopup" id="optionAttendPopupScroll" content="       " radioidx="2" />
          </CommonItemContent>
        </CommonItemWrapper>

        <CommonItemWrapper>
          <CommonItemContent title=" " multi={true}>
            <CheckItem name="optionAttendKakao" type="attend" id="optionAttendKakao" content="      " />
            <SettingNotice>
              <SettingNoticeContent>         . <br />( , ,  ,    )</SettingNoticeContent>
            </SettingNotice>
          </CommonItemContent>
        </CommonItemWrapper>

      </CommonOptionContent>
    </CommonOptionWrapper>
  )
}

export default AttendSetting;