/* Import */
import { useState, useContext } from "react";
/* Component */
import CommonOptionWrapper from "./CommonOptionWrapper.js";
import CommonOptionContent from "./CommonOptionContent.js";
import CommonItemWrapper from "./CommonItemWrapper.js";
import CommonItemContent from "./CommonItemContent.js";
import CoupleInfomationSettingBasic from "./CoupleInfomationSettingBasic.js";
import CoupleInfomationSettingParents from "./CoupleInfomationSettingParents.js";
import SettingNotice from "../layout/SettingNotice.js"
import SettingNoticeContent from "../layout/SettingNoticeContent.js"
import CheckItem from "./CheckItem.js";
import CoupleInfomationSettingMobileTab from "./CoupleInfomationSettingMobileTab.js";
/* Context */
import { InfoContext } from "../../store/option-info-context.js";
import { SetContext } from "../../store/option-set-context.js";

const parents = [
  {
    itemName: "",
    itemKey: "Dad"
  },
  {
    itemName: "",
    itemKey: "Mom"
  }
];
const CoupleInfomation = () => {
  const { isMobile } = useContext(SetContext);
  const { basicInfoList, setBasicInfoList } = useContext(InfoContext);
  const [ mobileTabActive, setMobileTabActive ] = useState("M");
  const basicDataChangeHandler = (e, key) => {
    const { name, value } = e.target;
    setBasicInfoList((prev) => ({
      ...prev,
      [key]: {
        ...prev[key],
        [name]: value, 
      },
    }));
  };
  return (
    <CommonOptionWrapper>
      {isMobile ? <CoupleInfomationSettingMobileTab mobileTabActive={mobileTabActive} onClick={setMobileTabActive} /> : null}
      {
        isMobile && mobileTabActive === "F" ?
        null :
        <CommonOptionContent style={isMobile ? {paddingBottom: 0, borderBottom: "none"} : null}>
          <CommonItemWrapper>
            <CommonItemContent title="" multi={isMobile}>
              <CoupleInfomationSettingBasic 
                couple="" 
                coupleKey="M" 
                value={basicInfoList.groomInfo} 
                onChange={(e) => basicDataChangeHandler(e, "groomInfo")}
              />
            </CommonItemContent>
            {parents.map((parent, idx) => (
              <CommonItemContent title={parent.itemName} key={`${parent.itemName}${idx}`} multi={isMobile}>
                <CoupleInfomationSettingParents 
                  itemKey={parent.itemKey} 
                  pName={parent.itemName} 
                  coupleKey="M"
                  data={basicInfoList.groomParentInfo}
                  onChange={(e) => basicDataChangeHandler(e, "groomParentInfo")}
                />
              </CommonItemContent>
            ))}
          </CommonItemWrapper>
        </CommonOptionContent>
      }
      {
        isMobile && mobileTabActive === "M" ?
        null :
        <CommonOptionContent style={isMobile ? {paddingBottom: 0, borderBottom: "none"} : null}>
          <CommonItemWrapper>
            <CommonItemContent title="" multi={isMobile}>
              <CoupleInfomationSettingBasic 
                couple="" 
                coupleKey="F" 
                value={basicInfoList.brideInfo} 
                onChange={(e) => basicDataChangeHandler(e, "brideInfo")}
              />
            </CommonItemContent>
            {parents.map((parent, idx) => (
              <CommonItemContent title={parent.itemName} key={`${parent.itemName}${idx}`} multi={isMobile}>
                <CoupleInfomationSettingParents 
                  itemKey={parent.itemKey} 
                  pName={parent.itemName} 
                  coupleKey="F"
                  data={basicInfoList.brideParentInfo}
                  onChange={(e) => basicDataChangeHandler(e, "brideParentInfo")}
                />
              </CommonItemContent>
            ))}
          </CommonItemWrapper>
        </CommonOptionContent>
      }

      <CommonOptionContent>
        <CommonItemWrapper>
          <CommonItemContent title=" " multi={!isMobile ? "check" : ""}>
            <CheckItem 
              name="deceasedFlower" 
              id="deceasedFlower" 
              disabled={false} 
              labelImgSrc={false} 
              content=" " 
            />
            {
              !isMobile ? 
              <SettingNotice>
                <SettingNoticeContent>,       .</SettingNoticeContent>
              </SettingNotice> : null
            }
          </CommonItemContent>
        </CommonItemWrapper>

      </CommonOptionContent>
      {
        isMobile ? 
        <SettingNotice>
          <SettingNoticeContent>,       .</SettingNoticeContent>
        </SettingNotice> : null
      }
    </CommonOptionWrapper>
  )
}

export default CoupleInfomation;