/* Import */
import { useContext } from "react";
import { DataChanger_NestedObject } from '../../utils/helpers';
/* Component */
import CommonOptionWrapper from "./CommonOptionWrapper.js";
import CommonOptionContent from "./CommonOptionContent.js";
import CommonItemWrapper from "./CommonItemWrapper.js";
import CommonItemContent from "./CommonItemContent.js";
import OptionSelector from "./OptionSelector.js";
import DateSettingImage from "./DateSettingImage.js";
import DateSettingCheckbox from "./DateSettingCheckbox.js";
/* Context */
import { InfoContext } from "../../store/option-info-context.js";
import { SetContext } from "../../store/option-set-context.js";

const dateHourList = [
  {itemKey: 8, itemName: " 8"}, {itemKey: 9, itemName: " 9"}, {itemKey: 10, itemName: " 10"}, {itemKey: 11, itemName: " 11"}, {itemKey: 12, itemName: " 12"}, {itemKey: 13, itemName: " 1"}, {itemKey: 14, itemName: " 2"}, {itemKey: 15, itemName: " 3"}, {itemKey: 16, itemName: " 4"}, {itemKey: 17, itemName: " 5"}, {itemKey: 18, itemName: " 6"}, {itemKey: 19, itemName: " 7"}, {itemKey: 20, itemName: " 8"}, {itemKey: 21, itemName: " 9"}
];
const dateMinuteList = [
  {itemKey: 0, itemName: ""}, {itemKey: 10, itemName: "10"}, {itemKey: 20, itemName: "20"}, {itemKey: 30, itemName: "30"}, {itemKey: 40, itemName: "40"}, {itemKey: 50, itemName: "50"}
];

// C:  
const DateSetting = () => {
  const { isMobile } = useContext(SetContext);
  const { basicInfoList, setBasicInfoList } = useContext(InfoContext);
  return (
    <CommonOptionWrapper>
      <CommonOptionContent>
        <CommonItemWrapper>
          <CommonItemContent title="">
            <input 
              type="date" 
              id="DateInfoDate" 
              name="date" 
              onChange={(e) => DataChanger_NestedObject(e, "dateInfo", setBasicInfoList)} 
              value={basicInfoList.dateInfo.date} 
            />
          </CommonItemContent>

          <CommonItemContent title="" wrap={isMobile}>
            <OptionSelector 
              selectID="DateInfoHour" 
              selectName="hour"
              listName={dateHourList} 
              value={basicInfoList.timeInfo.hour} 
              onChange={(e) => DataChanger_NestedObject(e, "timeInfo", setBasicInfoList)} 
            />
            <OptionSelector 
              selectID="DateInfoMin" 
              selectName="min" 
              listName={dateMinuteList} 
              value={basicInfoList.timeInfo.min} 
              onChange={(e) => DataChanger_NestedObject(e, "timeInfo", setBasicInfoList)} 
              styleType={"minute__selector"} 
            />
          </CommonItemContent>
          
          <DateSettingImage />
        </CommonItemWrapper>
      </CommonOptionContent>

      <CommonOptionContent>
        <CommonItemWrapper>
          <CommonItemContent title="">
            <DateSettingCheckbox />
          </CommonItemContent>
        </CommonItemWrapper>
      </CommonOptionContent>
    </CommonOptionWrapper>
  )
}

export default DateSetting;