/* Import */
import { useContext } from "react";
import { PHONE_NUMBER_REGEX, PHONE_NUMBER_AUTO_HYPHEN_REGEX, NAME_REGEX, NUMBER_LIMIT_ACCOUNT, NO_SPECIAL_CHAR } from "../constants/regex";
import imageCompression from "browser-image-compression"; 
/* Context */
import { IntroContext } from "../store/option-intro-context.js";


// ********** INPUT ********** //
export const autoHyphenHandler = (number) => ( // FUNC:   (-) 
  number.replace(PHONE_NUMBER_REGEX, "")
  .replace(PHONE_NUMBER_AUTO_HYPHEN_REGEX, (match, p1, p2, p3) => {
    if (p3) return `${p1}-${p2}-${p3}`;
    if (p2) return `${p1}-${p2}`;
    return p1;
  })
)
export const nameHandler = (data) => ( // FUNC:    
  data.replace(NAME_REGEX, "")
)
export const AccountHandler = (data) => ( // FUNC:      
  NUMBER_LIMIT_ACCOUNT.test(data) ? data : data.slice(0, 16)
)
export const AccountNameHandler = (data) => ( // FUNC:     
  data.replace(NO_SPECIAL_CHAR, "")
)
// ********** RADIO ********** //
export const checkedChanger_Object = (e, changer) => {
  const { name, id } = e.target;
  changer((prev) => ({
    ...prev,
    [name]: id
  }));
};

// ********** DATA CHANGER ********** //
export const DataChanger_NestedObject = (e, infoType, changer) => { // FUNC:     (onChange) -   name(e),  name(InfoType),   (changer) 
  const { name, value } = e.target;
  changer(prev => ({
    ...prev,
    [infoType]: {
      ...prev[infoType],
      [name]: value
    }
  }))
}
export const DataChanger_Object = (e, changer) => { // FUNC:    (onChange) -   name(e),  name(InfoType),   (changer) 
  const { name, value } = e.target;
  changer((prev) => ({
    ...prev,
    [name]: value
  }))
}
export const DataDeleter_Object = (e, changer) => { // FUNC:     (onChange)
  const { name } = e.target;
  changer((prev) => ({
    ...prev,
    [name]: ""
  }))
}
export const DataChanger_ObjectArray = (e, selectedIndex, changer) => { // FUNC:       (onChange),   name(e),   idx(selectedIndex),   (changer)
  const { name, value } = e.target;
  changer(prev => (
    prev.map((item, idx) => 
      idx === selectedIndex ? { ...item, [name]: value } : item 
    )
  ))
}
