// ********** PHOTO(INTRO) ********** //
export const IntroImgSeparator = (introIdx, type) => { // FUNC:   -    
  const { selectIntroPhoto } = useContext(IntroContext);
  const defaultImg = "/img/intro/intro_photo_empty.png";
  if (selectIntroPhoto.length === 0) {
    return type === "img" ? <img src={defaultImg} alt=" " /> : defaultImg;
  }
  const selectedPhoto = selectIntroPhoto[Math.min(introIdx, selectIntroPhoto.length - 1)];
  return type === "img" 
    ? <img src={selectedPhoto.src} alt={selectedPhoto.alt} /> 
    : selectedPhoto.src;
}
// ********** PHOTO(SELECTOR) ********** //
export const photoDeleter_ObjectArray = (deleteIdx, deleter) => { // FUNC: PhotoSelector -   
  deleter((prev) => {
    const newList = [...prev];
    newList[deleteIdx].src = ""; 
    return newList;
  });
}
export const fileAddHandler_ObjectArray = async (e, index, handler) => { // FUNC: PhotoSelector -    (,  )
  const file = e.target.files[index];
  if (file) {
    try {
      //   
      const options = { maxSizeMB: 1, maxWidthOrHeight: 800, useWebWorker: true };
  
      //  
      const compressedFile = await imageCompression(file, options);
  
      //  
      const fileReader = new FileReader();
      fileReader.onload = (event) => {
        handler((prev) => {
          //     
          const newList = [...prev];
          newList[index] = { 
            ...newList[index],
            fileName: file.name,   //  
            src: event.target.result, 
            alt: event.target.result,
          };
          return newList;
        });
      };
      fileReader.readAsDataURL(compressedFile);
    } catch (error) {
      console.error("    :", error);
    } finally {
      e.target.value = null; // input 
    }
  }
}


// ********** DATE ********** //
export const DayConverter = (date) => { // FUNC:    ->  
  const dateObj = new Date(date);
  const daysOfWeek = ['', '', '', '', '', '', '']; 
  const dayOfWeek = daysOfWeek[dateObj.getDay()]; 
  return dayOfWeek;
}
export const DayConverterENG = (date) => { // FUNC:    ->   
  const [, month] = date.split('-');
  const monthEng = (month) => {
    switch(month) {
      case "01":
        return "January";
      case "02":
        return "February";
      case "03":
        return "March";
      case "04":
        return "April";
      case "05":
        return "May";
      case "06":
        return "June";
      case "07":
        return "July";
      case "08":
        return "August";
      case "09":
        return "September";
      case "10":
        return "October";
      case "11":
        return "November";
      case "12":
        return "December";
      default:
        return;
    }
  }
  return monthEng(month);
}
export const DayConverterSlash = (date) => { // FUNC:    -> MM / DD 
  const selectedDate = date; 
  const formattedDate = selectedDate.split('-').join(' / ');
  return formattedDate;
};
export const DateConverter = (date) => { // FUNC:    ->  
  const selectedDate = date;
  const [year, month, day] = selectedDate.split('-');
  const formattedDate = `${year} ${month} ${day}`;
  return formattedDate;
}
export const DateConverterDot = (date) => { // FUNC:    -> YYYY. MM. DD. 
  const selectedDate = date; 
  const formattedDate = selectedDate.split('-').join('.');
  return formattedDate;
}
export const DateConverterMonth = (date) => {
  const selectedDate = date; 
  return selectedDate.substring(5, 7);
};
export const DateConverterDay = (date) => {
  const selectedDate = date; 
  return selectedDate.substring(8, 10);
};
export const DDayCalculator = (date) => { // FUNC:   
  const today = new Date();
  const selectedDate = new Date(date);
  const diffTime = selectedDate - today;
  const diffDays = Math.ceil(diffTime / (1000 * 60 * 60 * 24));
  return diffDays;
}
export const AddDaysToDate = (dateString, daysToAdd) => { // FUNC:  30 
  const date = new Date(dateString); 
  date.setDate(date.getDate() + daysToAdd);
  return date.toISOString().split("T")[0];
}

