/* CSS Module */
import styles from "../../css/module/common/LocationSettingName.module.scss";

// C:    -  ,   
const LocationSettingName = ({ name, locationValue, onChange }) => {
  return (
    <div className={styles.option__input}>
      {
        name === "placeName" ?
        <input 
          name="placeName" 
          type="text" 
          value={locationValue} 
          onChange={onChange}
          placeholder="  "
          maxLength={25}
        />
        :
        <input 
          name="placeDetail" 
          type="text" 
          value={locationValue} 
          onChange={onChange} 
          placeholder="  "
          maxLength={20}
        />
      }
    </div>
  )
}

export default LocationSettingName;