/* import */
import { useContext, useEffect, useState } from "react";
import { Link, useLocation } from "react-router-dom";
/* CSS Module */
import styles from "../../css/module/sub/SubHeader.module.scss";
/* Context */
import { SetContext } from "../../store/option-set-context.js";
/* Image */
import logoImg from "../../img/main/logo.svg";
import menuImg from "../../img/icon/icon_header_menu.png";
import closeImg from "../../img/icon/icon_header_menu_close.png";
import myInfo from "../../img/icon/icon_info.png";

const SubHeader = () => {
  const { isMobile } = useContext(SetContext);
  const [ activeMenu, setActiveMenu ] = useState(false);
  const location = useLocation();
  return (
    <div className={`${styles.sub__header} ${activeMenu && isMobile ? styles["active"] : ""}`}>
      <div className={`${styles.sub__header_wrapper}`}>
        <Link to="/Manager" className={styles.logo}>
          <img src={logoImg} alt=" " />
        </Link>
        <div>
          <ul className={`${styles.sub__menu}`}>
            <li><Link to="/Produce"> </Link></li>
            {/* <li><Link to="/"> </Link></li> */}
            <li><Link to="/Manager" className={location.pathname.includes("/Manager") ? styles.active : ""}> </Link></li>
            <li><Link to="/Wedding" className={location.pathname.includes("/Wedding") ? styles.active : ""}></Link></li>
            <li><Link to="/Notice" className={location.pathname.includes("/Notice") ? styles.active : ""}></Link></li>
            <li><Link to="/Qna" className={location.pathname.includes("/Qna") ? styles.active : ""}> </Link></li>
          </ul>
          <ul className={styles.sub__tool}>
            <li><Link tp="/"><img src={myInfo} alt="" /><span></span></Link></li>
            <li><Link tp="/"><span></span></Link></li>
          </ul>
        </div>
        {isMobile && (
          <button
            type="button"
            className={styles.sub__menu_mo}
            onClick={() => setActiveMenu((prev) => !prev)}
          >
            <img src={activeMenu ? closeImg : menuImg} alt={activeMenu ? "Close Menu" : "Open Menu"} />
          </button>
        )}
      </div>
    </div>
  )
}

export default SubHeader;