/* Import */
import { useContext } from "react";
/* Component */
import CommonOptionWrapper from "./CommonOptionWrapper.js";
import CommonOptionContent from "./CommonOptionContent.js";
import CommonItemWrapper from "./CommonItemWrapper.js";
import CommonItemContent from "./CommonItemContent.js";
import SettingNotice from "../layout/SettingNotice";
import SettingNoticeContent from "../layout/SettingNoticeContent";
/* Context */
import { SetContext } from "../../store/option-set-context.js";

// C:  -   
const GuestbookSetting = () => {
  const { guestbookAdminPassword, setGuestbookAdminPassword } = useContext(SetContext);
  const passwordChangeHandler = (e) => {
    const { value } = e.target;
    setGuestbookAdminPassword(value);
  }
  return (
    <CommonOptionWrapper>
      <CommonOptionContent>
        <CommonItemWrapper>
          <CommonItemContent title='' multi={true}>
            <input 
              type='password' 
              value={guestbookAdminPassword}
              onChange={(e) => passwordChangeHandler(e)}
              placeholder="  ."
              maxLength={20}
            />
            <SettingNotice>
              <SettingNoticeContent>   .</SettingNoticeContent>
              <SettingNoticeContent>     .</SettingNoticeContent>
            </SettingNotice>
          </CommonItemContent>
        </CommonItemWrapper>
      </CommonOptionContent>
    </CommonOptionWrapper>
  );
};

export default GuestbookSetting;
