// C: /  - , , 
const CoupleInfomationSettingBasic = ({ coupleKey, value, onChange }) => {
  const [relList, setRelList] = useState([]);
  useEffect(() => {
    coupleKey === "M" ?
    setRelList(MRelList)
    :
    setRelList(FRelList)
  }, [coupleKey])

  return (
    <div className={styles.couple__info}>
      <input 
        type="text" 
        id={`${coupleKey}FirstName`}
        name="lastname" 
        className={styles.info__firstname} 
        placeholder={``} 
        value={nameHandler(value.lastname)}
        onChange={onChange}
        maxLength={2}
      />
      <input 
        type="text" 
        id={`${coupleKey}Name`} 
        name="firstname" 
        className={styles.info__name} 
        placeholder={``} 
        value={nameHandler(value.firstname)}
        onChange={onChange}
        maxLength={3}
      />
      <input 
        type="text" 
        id={`${coupleKey}Number`} 
        name="number" 
        className={styles.info__number} 
        placeholder="  -  "
        maxLength={13}
        value={autoHyphenHandler(value.number)}
        onChange={onChange}
      />
      <OptionSelector 
        selectID={`${coupleKey}RelSelect`}
        selectName="relation"
        className={styles.info__firstname_selector}
        listName={relList}
        defaultValue={value.relation}
        value={value.relation}
        onChange={onChange}
      />
    </div>
  )
}

export default CoupleInfomationSettingBasic;