/* Component */
import OptionSelector from "./OptionSelector.js";
/* CSS Module */
import styles from "../../css/module/common/BasicInfomationSettingDate.module.scss";

const dateHourList = [
  {itemKey: 8, itemName: " 8"}, {itemKey: 9, itemName: " 9"}, {itemKey: 10, itemName: " 10"}, {itemKey: 11, itemName: " 11"}, {itemKey: 12, itemName: " 12"}, {itemKey: 13, itemName: " 1"}, {itemKey: 14, itemName: " 2"}, {itemKey: 15, itemName: " 3"}, {itemKey: 16, itemName: " 4"}, {itemKey: 17, itemName: " 5"}, {itemKey: 18, itemName: " 6"}, {itemKey: 19, itemName: " 7"}, {itemKey: 20, itemName: " 8"}, {itemKey: 21, itemName: " 9"}
];
const dateMinuteList = [
  {itemKey: 0, itemName: ""}, {itemKey: 10, itemName: "10"}, {itemKey: 20, itemName: "20"}, {itemKey: 30, itemName: "30"}, {itemKey: 40, itemName: "40"}, {itemKey: 50, itemName: "50"}
];

// C:    - ,   (BasicInfomationSetting)
const BasicInfomationSettingDate = ({ inputType, onChange, dateInfo, timeInfo }) => {
  return (
    <div className={styles.option__input}>
      {inputType === "date" ? 
        <input 
          type="date" 
          id="BasicInfoDate" 
          name="date" 
          onChange={onChange} 
          value={dateInfo.date} 
        />
        :
        <>
          <OptionSelector 
            selectID="BasicInfoHour" 
            selectName="hour" 
            listName={dateHourList} 
            value={timeInfo.hour} 
            onChange={onChange} 
          />
          <OptionSelector 
            selectID="BasicInfoMin" 
            selectName="min" 
            listName={dateMinuteList} 
            value={timeInfo.min} 
            onChange={onChange} styleType={"minute__selector"} 
          />
        </>
      }
    </div>
  )
}

export default BasicInfomationSettingDate;